}



/* input form */
.dnata-input_wrapper{
  position: relative;
  /* margin-bottom: 1rem; */
  /* width:100%;
  height: 50px;
  background-color: #ededed;
  border-radius: 10px; */
 }
.dnata-input_wrapper label{
  /* position: absolute; */
  font-size: 1rem;
  top: 1rem;
  left:4rem;
  color: #5f5753;
  z-index: 5;
  line-height: 1.5rem;
  margin: 0;
  max-width: 85%;
  transition: top 250ms ease-in-out,font-size 250ms ease-in-out;
  -webkit-backface-visibility: hidden;
  width: 100%;
  display: inline-block;
  overflow-x: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.input-container{
  width: 100%;
  margin:0 auto;
  height: 50px;
  border-radius: 10px;
  border: none;
  color: #5f5753;
  font-size: 1rem;
  background-color: #ededed;
  transition: all 150ms ease-in-out;
  position: relative;
}
.tracking-input{
   width: 80%;
  height: 90%;
  left:5%;
  /* padding-left:15%; */
  /*padding: 1.5rem 2rem 0.5rem 0.75rem;
  border-radius: 0.25rem;
  text-align: left;
  color: #121212;*/
  background-color: transparent; 
  border: none;
  outline: none;
  appearance: none;
  -webkit-appearance: none;
  user-select: none;
  z-index: 10;
  position: relative;
  display: block;
}
.search-icon{
  position:absolute;
  top:30%;
  left:0.5rem;
  transition: transform .35s ease-in-out;
    transform: translateX(0);
    margin-left: 0.5rem;
    display: inline-block;
    font-size: .75rem;
} 

/* button */
.btn{
  margin-top:20px;
  /* margin:auto; */
  width:100%;
  height: 50px;
  background-color: #0085CA;
  border-radius: 10px;
  text-align: center;
  color:#fff;
  margin-bottom:30px;
}


